import axios from "@/plugins/axios";
export default {
  state: () => ({ goods: [], total: 0 }),
  mutations: {
    getGoods(state, payload) {
      state.goods = payload.goods;
      state.total = payload.total;
    },
    editGoodsItem(state, payload) {
      const ind = state.goods.findIndex((el) => el.goods_id === payload.id);
      state.goods.splice(ind, 1, payload.data);
    },
    delGoodsItem(state, id) {
      state.goods = state.goods.filter((el) => el.goods_id !== id);
      state.total--;
    },
    addGoods(state, payload) {
      state.goods.push(payload);
    },
  },
  actions: {
    // 获取所有商品
    async getGoods({ commit }, payload) {
      const res = await axios.get("/goods", { params: payload });
      if (res.meta.status === 200) {
        commit("getGoods", res.data);
      }
    },
    // 修改商品信息
    editGoodsItem({ commit }, payload) {
      // console.log(payload);
      return new Promise((resolve, reject) => {
        axios.put("goods/" + payload.id, payload.data).then((res) => {
          if (res.meta.status === 200) {
            commit("editGoodsItem", { data: res.data, id: payload.id });
            resolve();
          } else {
            reject(res.meta.msg);
          }
        });
      });
    },
    // 删除商品
    delGoodsItem({ commit }, id) {
      return new Promise((resolve, reject) => {
        axios.delete("goods/" + id).then((res) => {
          if (res.meta.status === 200) {
            commit("delGoodsItem", id);
            resolve();
          } else {
            reject(res.meta.msg);
          }
        });
      });
    },
    addGoods({ commit }, payload) {
      console.log(payload);
      return new Promise((resolve, reject) => {
        axios.post("goods", payload).then((res) => {
          if (res.meta.status === 201) {
            commit("addGoods", res.data);
            resolve();
          } else {
            reject(res.meta.msg);
          }
        });
      });
    },
  },
};
