import axios from "@/plugins/axios";
export default {
  state: () => ({ menuList: [] }),
  mutations: {
    getMenuList(state, payload) {
      state.menuList = payload;
    },
  },
  actions: {
    // 获取所有菜单
    async getMenuList({ commit }) {
      const res = await axios.get("/menus");
      if (res.meta.status === 200) {
        commit("getMenuList", res.data);
      }
    },
  },
};
