export default function (arrs, ids) {
  let obj = null;
  const findObj = (arr, id) => {
    for (let i = 0; i < arr.length; i++) {
      if (arr[i].cat_id === id) {
        // console.log("找到了", arr[i]);
        obj = arr[i];
      } else {
        if (arr[i].children) {
          findObj(arr[i].children, id);
        }
      }
    }
  };
  findObj(arrs, ids);
  // console.log(obj);
  return obj;
}
