import axios from "@/plugins/axios";
export default {
  state: () => ({
    roles: [],
    allRights: [],
  }),
  mutations: {
    getRoles(state, payload) {
      state.roles = payload;
    },
    addRoles(state, payload) {
      state.roles.push(payload);
    },
    delRoles(state, id) {
      state.roles = state.roles.filter((el) => el.id !== id);
    },
    editRoles(state, payload) {
      const ind = state.roles.findIndex((el) => el.id === payload.id);
      state.roles.splice(ind, 1, payload);
    },
    delRolesLimits(state, payload) {
      state.roles.find((el) => el.id === payload.id).children = payload.data;
    },
    getAllRights(state, payload) {
      state.allRights = payload;
    },
  },
  actions: {
    // 获取角色列表
    async getRoles({ commit }) {
      const res = await axios.get("/roles");
      console.log(res);
      if (res.meta.status === 200) {
        commit("getRoles", res.data);
      }
    },
    // 添加角色
    addRoles({ commit }, payload) {
      return new Promise((resolve, reject) => {
        axios.post("/roles", payload).then((res) => {
          if (res.meta.status === 201) {
            commit("addRoles", res.data);
            resolve();
          } else {
            reject(res.meta.msg);
          }
        });
      });
    },
    // 删除角色
    delRoles({ commit }, id) {
      return new Promise((resolve, reject) => {
        axios.delete(`roles/${id}`).then((res) => {
          if (res.meta.status === 200) {
            commit("delRoles", id);
            resolve();
          } else {
            reject(res.meta.msg);
          }
        });
      });
    },
    // 编辑角色
    editRoles({ commit }, payload) {
      return new Promise((resolve, reject) => {
        axios.put("roles/" + payload.id, payload).then((res) => {
          if (res.meta.status === 200) {
            commit("editRoles", res.data);
            resolve();
          } else {
            reject(res.meta.msg);
          }
        });
      });
    },
    // 删除角色权限
    delRolesLimits({ commit }, payload) {
      // console.log(payload);
      return new Promise((resolve, reject) => {
        axios
          .delete(`roles/${payload.roleId}/rights/${payload.rightId}`)
          .then((res) => {
            if (res.meta.status === 200) {
              // console.log(res.data);
              commit("delRolesLimits", { id: payload.roleId, data: res.data });
              resolve();
            } else {
              reject(res.meta.msg);
            }
          });
      });
    },
    // 获取所有权限
    async getAllRights({ commit }, type) {
      const res = await axios.get("rights/" + type);
      if (res.meta.status === 200) {
        commit("getAllRights", res.data);
      }
    },
    // 角色授权
    roleAuthorization({ dispatch }, payload) {
      return new Promise((resolve, reject) => {
        axios
          .post(`roles/${payload.id}/rights`, {
            rids: payload.rids,
          })
          .then((res) => {
            if (res.meta.status === 200) {
              dispatch("getRoles");
              resolve();
            } else {
              reject(res.meta.msg);
            }
          });
      });
    },
  },
};
