<template>
  <div class="menu">
    <template v-for="item in menu">
      <el-submenu
        :index="item.path"
        :key="item.id"
        v-if="item.children.length && item.children"
      >
        <template slot="title">
          <i :class="iconsObj[item.id]"></i>
          <span>{{ item.authName }}</span>
        </template>
        <Menus
          :menu="item.children.length && item.children"
          v-if="item.children.length && item.children"
        />
      </el-submenu>
      <el-menu-item :index="'/' + item.path" :key="item.id" v-else>
        <i class="el-icon-menu"></i>
        <span slot="title">{{ item.authName }}</span>
      </el-menu-item>
    </template>
  </div>
</template>

<script>
export default {
  name: "Menus",
  props: ["menu"],
  data() {
    return {
      iconsObj: {
        125: "iconfont icon-user",
        103: "iconfont icon-tijikongjian",
        101: "iconfont icon-shangpin",
        102: "iconfont icon-danju",
        145: "iconfont icon-baobiao",
      },
    };
  },
};
</script>

<style lang="less">
.menu {
  span {
    font-size: 12px;
    margin-left: 8px;
  }
}
</style>
