export default function (arrs, ids) {
  let newArr = null;
  let count = 0;
  const findObj = (arr, id) => {
    for (let i = 0; i < arr.length; i++) {
      if (arr[i].cat_id === id) {
        // console.log("找到了", arr[i]);
        newArr = arr;
      } else {
        if (arr[i].children) {
          count++;
          findObj(arr[i].children, id);
        }
      }
    }
  };
  findObj(arrs, ids);
  // console.log(obj);
  return { newArr, count };
}
