import axios from "@/plugins/axios";
export default {
  state: () => ({ orders: [], total: 0, physics: [] }),
  mutations: {
    getOrders(state, payload) {
      state.orders = payload.goods;
      state.total = payload.total;
    },
    getPhysics(state, res) {
      state.physics = res;
    },
  },
  actions: {
    // 获取订单列表
    async getOrders({ commit }, payload) {
      const res = await axios.get("/orders", { params: payload });
      if (res.meta.status === 200) {
        commit("getOrders", res.data);
      }
    },
    // 编辑地区信息
    //物流信息
    /*  async getPhysics({ commit }, id) {
      const res = await axios.get("/kuaidi/" + id);
      console.log(res);
      if (res.meta.status === 200) {
        commit("getPhysics", res.data);
      }
    }, */
  },
};
