import axios from "@/plugins/axios";
export default {
  state: () => ({ attributes: [] }),
  mutations: {
    getAttributes(state, payload) {
      state.attributes = payload;
    },
    addAttributes(state, payload) {
      state.attributes.push(payload);
    },
    saveAttributes(state, payload) {
      state.attributes.find((el) => el.attr_id === payload.attr_id).attr_name =
        payload.attr_name;
    },
  },
  actions: {
    // 获取参数列表
    async getAttributes({ commit }, payload) {
      console.log(payload);
      const res = await axios.get(
        `categories/${payload.id}/attributes?sel=${payload.sel}`
      );
      if (res.meta.status === 200) {
        //console.log(res.data);
        commit("getAttributes", res.data);
      }
    },
    // 编辑提交参数
    async saveAttributes({ commit }, payload) {
      const res = await axios.put(
        `categories/${payload.cateId}/attributes/${payload.attr_id}}`,
        {
          attr_name: payload.attr_name,
          attr_sel: payload.attr_sel,
          attr_vals: payload.attr_vals,
        }
      );
      if (res.meta.status === 200) {
        commit("saveAttributes", res.data);
      }
    },
    // 删除参数
    async delAttributes({ commit }, payload) {
      const res = await axios.delete(
        `categories/${payload.cateId}/attributes/${payload.attr_id}`
      );
      console.log(res);
    },
    // 添加动态参数
    addAttributes({ commit }, payload) {
      return new Promise((resolve, reject) => {
        axios
          .post(`categories/${payload.catId}/attributes`, payload.data)
          .then((res) => {
            if (res.meta.status === 201) {
              commit("addAttributes", res.data);
              resolve();
            } else {
              reject(res.meta.msg);
            }
          });
      });
    },
  },
};
