// 商品分类
import axios from "@/plugins/axios";
import findObj from "@/untils/index";
import del from "@/untils/del";
export default {
  state: () => ({ categoryList: [], total: 5, categoryAllList: [] }),
  mutations: {
    getGoodsCategory(state, payload) {
      state.categoryList = payload.result;
      state.total = payload.total;
    },
    getCategoryAllList(state, payload) {
      state.categoryAllList = payload;
    },
    addCategoriesGoodItem(state, payload) {
      if (payload.id === 0) {
        state.total++;
        state.categoryList.push(payload.data);
      } else {
        const obj = findObj(state.categoryList, payload.id);
        // console.log(obj);
        obj.children.push(payload.data);
      }
    },
    editCategoriesGoodItem(state, payload) {
      const obj = findObj(state.categoryList, payload.id);
      // console.log("find", obj);
      obj.cat_name = payload.cat_name;
    },
    delCategoriesGoodItem(state, id) {
      const { newArr, count } = del(state.categoryList, id);
      const ind = newArr.findIndex((el) => el.cat_id === id);
      newArr.splice(ind, 1);
      if (count == 0) {
        state.total--;
      }
    },
  },
  actions: {
    // 获取所有商品分类数据列表
    async getGoodsCategory({ commit }, query) {
      // console.log(query);
      const res = await axios.get("/categories", { params: query });
      if (res.meta.status === 200) {
        if (!query.type) {
          commit("getGoodsCategory", res.data);
        } else if (query.type) {
          commit("getCategoryAllList", res.data);
        }
      }
    },
    // 添加商品分类
    addCategoriesGoodItem({ commit }, payload) {
      return new Promise((resolve, reject) => {
        axios.post("/categories", payload).then((res) => {
          if (res.meta.status === 201) {
            commit("addCategoriesGoodItem", {
              id: payload.cat_pid,
              data: res.data,
            });
            resolve();
          } else {
            reject(res.meta.msg);
          }
        });
      });
    },
    // 编辑商品分类
    editCategoriesGoodItem({ commit }, payload) {
      //console.log(payload.id);
      return new Promise((resolve, reject) => {
        axios
          .put("/categories/" + payload.id, {
            cat_name: payload.cat_name,
          })
          .then((res) => {
            if (res.meta.status === 200) {
              //console.log(res);
              commit("editCategoriesGoodItem", payload);
              resolve();
            } else {
              reject(res.meta.msg);
            }
          });
      });
    },
    // 删除商品分类
    delCategoriesGoodItem({ commit }, id) {
      return new Promise((resolve, reject) => {
        axios.delete("categories/" + id).then((res) => {
          if (res.meta.status === 200) {
            commit("delCategoriesGoodItem", id);
            resolve();
          } else {
            reject(res.meta.msg);
          }
        });
      });
    },
  },
};
