<template>
  <div class="login">
    <div class="login-container">
      <h2>系统登录</h2>
      <el-form ref="form" :model="form" :rules="rules">
        <el-form-item prop="username">
          <el-input
            v-model="form.username"
            prefix-icon="el-icon-s-custom"
            placeholder="账号"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            v-model="form.password"
            prefix-icon="el-icon-lock"
            placeholder="密码"
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            :disabled="!flag"
            @click="onSubmit"
            class="login-button"
            :loading="loading"
            >登录</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: { username: "admin", password: "123456" },
      rules: {
        username: [
          {
            required: true,
            message: "请输入账号",
            trigger: "blur",
            transform(value) {
              return value.trim();
            },
          },
        ],
        password: [
          {
            required: true,
            message: "请输入密码",
            trigger: "blur",
            transform(value) {
              return value.trim();
            },
          },
        ],
      },
      loading: false,
    };
  },
  computed: {
    flag() {
      return this.form.username.trim() && this.form.password;
    },
  },
  methods: {
    onSubmit() {
      this.$refs.form.validate((validate) => {
        if (validate) {
          this.$axios
            .post("login", {
              username: this.form.username,
              password: this.form.password,
            })
            .then((res) => {
              if (res.meta.status !== 200)
                return this.$message.error("登录失败！");
              window.sessionStorage.setItem("token", res.data.token);
              this.$router.push("/home");
            });
        }
      });
    },
  },
};
</script>

<style lang="less">
.login {
  width: 100vw;
  height: 100vh;
  background-color: #2d3a4b;
  .login-container {
    display: flex;
    width: 520px;
    margin: 0 auto;
    flex-direction: column;
    align-items: center;
    padding: 160px 35px 0;

    h2 {
      margin: 0;
      color: #fff;
      font-size: 26px;
      color: #eee;
      text-align: center;
      font-weight: 700;
      margin-bottom: 30px;
    }
    .el-form {
      width: 100%;
    }
    .el-input__inner {
      width: 100%;
      line-height: 50px;
      height: 50px;
      background-color: rgba(0, 0, 0, 0.1);
      border: 1px solid #3e4856;
      color: #fff;
      text-indent: 15px;
    }
    .el-input__icon {
      color: #889aa4;
      font-size: 16px;
      margin-left: 10px;
    }
    .login-button {
      width: 100%;
    }
  }
}
</style>
