import axios from "@/plugins/axios";

export default {
  state: () => ({
    userListInfor: null,
    userList: [],
    limits: [],
    total: 0,
  }),
  mutations: {
    getUserListInfor(state, payload) {
      state.userListInfor = payload;
      state.userList = payload.users;
      state.total = payload.total;
    },
    changeUserStatus(state, payload) {
      state.userList.find((el) => el.id === payload.id).mg_state =
        payload.mg_state;
    },
    delUserItem(state, id) {
      state.userList = state.userList.filter((el) => el.id !== id);
      state.total--;
    },
    addUserItem(state, payload) {
      if (state.userList.length < 10) {
        state.userList.push(payload);
        state.total++;
      } else {
        state.total++;
      }
    },
    editUserItem(state, payload) {
      const ind = state.userList.findIndex((el) => el.id === payload.id);
      state.userList.splice(ind, 1, payload);
    },
    getLimits(state, payload) {
      state.limits = payload;
    },
    setLimits(state, payload) {
      const roleName = state.limits.find(
        (el) => el.id === payload.role
      ).roleName;
      state.userList.find((el) => el.id === payload.id).role_name = roleName;
    },
  },
  actions: {
    // 获取user列表信息
    async getUserList({ commit }, payload) {
      const res = await axios.get("/users", {
        params: payload,
      });
      if (res.meta.status === 200) {
        commit("getUserListInfor", res.data);
      }
    },
    // 更改角色状态
    async changeUserStatus({ commit }, payload) {
      const res = await axios.put(
        `users/${payload.id}/state/${payload.mg_state}`
      );
      if (res.meta.status === 200) {
        commit("changeUserStatus", payload);
      }
    },
    // 删除角色
    delUserItem({ commit }, id) {
      return new Promise((resolve, reject) => {
        axios.delete("users/" + id).then((res) => {
          if (res.meta.status === 200) {
            commit("delUserItem", id);
            resolve();
          } else {
            reject(res.meta.msg);
          }
        });
      });
    },
    // 添加角色
    addUserItem({ commit }, payload) {
      delete payload.id;
      return new Promise((resolve, reject) => {
        axios.post("/users", payload).then(
          (res) => {
            commit("addUserItem", res.data);
            resolve();
          },
          (error) => {
            reject(error);
          }
        );
      });
    },
    // 编辑角色
    editUserItem({ commit }, payload) {
      console.log(payload);
      const newUser = {
        id: payload.id,
        email: payload.email,
        mobile: payload.mobile,
      };
      return new Promise((resolve, reject) => {
        axios.put("users/" + payload.id, newUser).then((res) => {
          if (res.meta.status === 200) {
            commit("editUserItem", payload);
            resolve();
          } else {
            reject(res.meta.msg);
          }
        });
      });
    },
    // 获取所有角色
    async getLimits({ commit }) {
      const res = await axios.get("/roles");
      if (res.meta.status === 200) {
        commit("getLimits", res.data);
      }
    },
    // 更改角色
    setLimits({ commit }, payload) {
      console.log(payload);
      return new Promise((resolve, reject) => {
        axios
          .put(`users/${payload.id}/role`, { rid: payload.role })
          .then((res) => {
            if (res.meta.status === 200) {
              commit("setLimits", payload);
              resolve();
            } else {
              reject(res.meta.msg);
            }
          });
      });
    },
  },
};
