import Vue from "vue";
import VueRouter from "vue-router";
import Login from "@/views/Login.vue";
import Home from "@/views/Home.vue";
Vue.use(VueRouter);

const routes = [
  { path: "/login", component: Login },
  { path: "/", redirect: "/login" },
  {
    path: "/home",
    component: Home,
    redirect: "/welcome",
    children: [
      { path: "/welcome", component: () => import("../views/Welcome.vue") },
      { path: "/users", component: () => import("../views/User.vue") },
      { path: "/roles", component: () => import("../views/Roles.vue") },
      { path: "/rights", component: () => import("../views/Rights.vue") },
      { path: "/goods", component: () => import("../views/Goods.vue") },
      {
        path: "/goods/add",
        component: () => import("../views/AddGoods.vue"),
      },
      {
        path: "/categories",
        component: () => import("../views/Categories.vue"),
      },
      {
        path: "/params",
        component: () => import("../views/Params.vue"),
      },
      {
        path: "/reports",
        component: () => import("../views/Reports.vue"),
      },
      {
        path: "/orders",
        component: () => import("../views/Ordes.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  mode: "hash",
  routes,
  base: process.env.BASE_URL,
});
router.beforeEach((to, from, next) => {
  if (to.path === "/login") {
    next();
  } else {
    const token = window.sessionStorage.getItem("token");
    if (token) {
      next();
    } else {
      next("/login");
    }
  }
});
export default router;
