<template>
  <div class="home">
    <el-container>
      <el-header class="header">
        <div class="header-left">
          <img src="../assets/logo.png" alt="" />
          <h2>电商管理后台系统</h2>
        </div>
        <el-button type="info" @click="quit">退出</el-button>
      </el-header>
      <el-container>
        <el-aside width="220px">
          <el-menu
            background-color="#2e3e52"
            text-color="#fff"
            active-text-color="#409eff"
            router
            :default-active="defaultMenu"
            @select="select"
            unique-opened
          >
            <Menus :menu="menuList" />
          </el-menu>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Menus from "../components/Menus.vue";
import { mapState, mapActions } from "vuex";
export default {
  components: { Menus },
  data() {
    return {
      defaultMenu: "",
    };
  },
  created() {
    this.getMenuList();
    // 获取默认菜单
    this.defaultMenu = window.sessionStorage.getItem("defaultMenu");
  },
  computed: {
    ...mapState({
      menuList: (state) => state.home.menuList,
    }),
  },
  methods: {
    quit() {
      window.sessionStorage.removeItem("token");
      this.$router.push("/login");
    },
    select(index) {
      window.sessionStorage.setItem("defaultMenu", index);
    },
    ...mapActions(["getMenuList"]),
  },
};
</script>

<style lang="less">
.home {
  height: 100%;
  .header {
    height: 60px;
    background-color: #2e3e52;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .header-left {
      display: flex;
      align-items: center;
      img {
        width: 50px;
        height: 60px;
      }
      h2 {
        color: #fff;
        margin-left: 15px;
      }
    }
  }
  .el-container {
    height: 100%;
    background-color: #eaedf1;
  }
  .el-menu {
    height: 100%;
  }
  .el-main {
    padding: 20px;
  }
}
</style>
